.yc-card {
  --yc-card-box-shadow: none;
  --yc-card-border-radius: 8px;
  background-color: transparent;
  box-shadow: none;
  box-shadow: var(--yc-card-box-shadow);
  box-sizing: border-box;
  border-radius: 8px;
  border-radius: var(--yc-card-border-radius);
  outline: none;
}
.yc-card_type_action {
  background-color: var(--g-color-base-float);
  --yc-card-box-shadow: 0px 1px 5px var(--g-color-sfx-shadow);
}
.yc-card_type_action::after {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  border-radius: var(--yc-card-border-radius);
  pointer-events: none;
}
.yc-card_type_action.yc-card_clickable {
  cursor: pointer;
  position: relative;
}
.yc-card_type_action.yc-card_clickable:hover {
  --yc-card-box-shadow: 0px 3px 10px var(--g-color-sfx-shadow);
}
