.yc-dropdown-menu__switcher-wrapper {
  display: inline-block;
}
.yc-dropdown-menu__switcher-button {
  display: flex;
}
.yc-dropdown-menu__menu-item_separator {
  border-top: 1px solid var(--g-color-line-generic-solid);
  margin: 0.5em 0;
  pointer-events: none;
}
.yc-dropdown-menu__menu-item-content {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 16px;
}
.yc-dropdown-menu__menu-item-content-children {
  flex: auto;
}
.yc-dropdown-menu__sub-menu {
  position: relative;
}
.yc-dropdown-menu__sub-menu .yc-dropdown-menu__menu:before, .yc-dropdown-menu__sub-menu .yc-dropdown-menu__menu:after {
  position: absolute;
  top: 0;
  width: 10px;
  height: 100%;
  content: "";
}
.yc-dropdown-menu__sub-menu .yc-dropdown-menu__menu:before {
  left: -10px;
}
.yc-dropdown-menu__sub-menu .yc-dropdown-menu__menu:after {
  right: -10px;
}